import Vue from 'vue'
import App from './App'
import 'pl-table/themes/index.css' // webpackurl-loaderwoffttf,
import 'pl-table/themes/plTableStyle.css'
import plTable from 'pl-table'
Vue.use(plTable)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>'
})



