<template>
  <!--  useVirtual   -->
  <!-- 1.  2. this.$refs.plTable.setHeight() //  -->
  <div style="height: 100%;width: 100%">
   <p style="color: red"> pl-tableStar</p>
    <div style="height: 500px;width: 100%;overflow: auto">
      <!--<pl-table :datas="tableData" height="400" useVirtual
       height(ele)
      row-height   big-data-checkbox
      -->
      <pl-table :datas="tableData"
                big-data-checkbox
                ref="plTable"
                use-virtual
                header-drag-style
                :row-height="50">
        <pl-table-column type="selection" width="55"/>
        <pl-table-column type="index" width="100" fixed/>
        <!--show-overflow-tooltip-->
        <pl-table-column
          v-for="item in columns"
          :key="item.id"
          :resizable="item.resizable"
          :show-overflow-tooltip="item.showOverflowTooltip"
          :prop="item.prop"
          :label="item.label"
          :fixed="item.fixed"
          :width="item.width"/>
      </pl-table>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        columns: [
          {prop: 'date', label: '', width: 120},
          {prop: 'address', label: '', width: 100, showOverflowTooltip: true},
          {prop: 'address', label: '', width: 100},
          {prop: 'address', label: '', width: 100},
          {prop: 'address', label: '', width: 100, showOverflowTooltip: true},
          {prop: 'address', label: '', width: 100},
          {prop: 'address', label: '', width: 100},
          {prop: 'address', label: '', width: 100, showOverflowTooltip: true},
          {prop: 'address', label: '', width: 230, showOverflowTooltip: true},
          {prop: 'ab', label: '', width: 100, fixed: 'right', resizable: false},
        ],
        tableData: Array.from({length: 10000}, (_, idx) => ({
          idx: idx + 1,
          date: '2016-05-03',
          name: '',
          ab: 'pl-table',
          address: ' 1516 '
        }))
      }
    }
  }
</script>

<style>
  body, html {
    margin: 0;
    padding: 10px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
  }
</style>



