              sums[index] = values.reduce((prev, curr) => {
                const value = Number(curr)
                if (!isNaN(value)) {
                  return prev + curr
                } else {
                  return prev
                }
              }, 0)
              sums[index] += flas[0].unit || ''
            } else {
              sums[index] = ''
            }
          })
          return sums
